<template lang="html">
    <div class="profile-info__form-group">
        <div class="pprofile-info__form-row">
            <div class="profile-info__col50 mt-0">
                <form-input
                    v-model="$v.address.$model"
                    outlined
                    legend="Address"
                    :field.sync="address"
                    :errors="validationMessage($v.address)"
                    :is-valid="$v.address.$dirty && !$v.address.$anyError"
                    @input.native="$v.address.$touch()"
                    @blur.native="$v.address.$touch()"
                />
            </div>
        </div>
        <div class="profile-info__form-row">
            <div class="profile-info__col50">
                <form-input
                    v-model="city"
                    legend="City"
                    :field.sync="city"
                    :errors="validationMessage($v.city)"
                    :is-valid="$v.city.$dirty && !$v.city.$anyError"
                    @input.native="$v.city.$touch()"
                    @blur.native="$v.city.$touch()"
                />
            </div>
            <div class="profile-info__col50">
                <form-input
                    v-model="zip"
                    legend="ZIP"
                    :field.sync="zip"
                    :errors="validationMessage($v.zip)"
                    :is-valid="$v.zip.$dirty && !$v.zip.$anyError"
                    @input.native="$v.zip.$touch()"
                    @blur.native="$v.zip.$touch()"
                />
            </div>
        </div>
        <div class="profile-info__form-row">
            <div class="profile-info__col50">
                <form-select-countries
                    v-model="$v.country.$model"
                    :country="country"
                    label="Country"
                    tabindex="0"
                    :field.sync="country"
                    :errors="validationMessage($v.country)"
                    :is-valid="!$v.country.$anyError && !$v.country.$invalid"
                    @input.native="$v.country.$touch()"
                    @blur.native="$v.country.$touch()"
                />
            </div>
            <div
                v-if="regionsLength > 0"
                class="profile-info__col50"
            >
                <form-select-region
                    v-model="$v.region.$model"
                    :region="region"
                    :country="country"
                    label="Region"
                    tabindex="0"
                    :field.sync="region"
                    :errors="validationMessage($v.region)"
                    :is-valid="!$v.region.$anyError && !$v.region.$invalid"
                    @input.native="$v.region.$touch()"
                    @blur.native="$v.region.$touch()"
                    @checkRequired="regionsLength = $event"
                />
            </div>
            <div
                v-else
                class="profile-info__col50"
            >
                <form-select-region
                    v-model="$v.region.$model"
                    :region="region"
                    :country="country"
                    label="Region"
                    tabindex="0"
                    :field.sync="region"
                    :errors="validationMessage($v.region)"
                    :is-valid="!$v.region.$anyError && !$v.region.$invalid"
                    @input.native="$v.region.$touch()"
                    @blur.native="$v.region.$touch()"
                    @checkRequired="regionsLength = $event"
                />
            </div>
        </div>
    </div>
</template>

<script>
import { eventBus } from '@/helpers/event-bus/'
// Vuelidate
import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/profile/ProfileAddress/Rules'
import { formMessages } from '@/validation/profile/ProfileAddress/Messages'

// VUEX
import {
    mapClientFields
} from '@/store/modules/client';

export default {
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    validations: {
        ...validationRules
    },
    data() {
        return {
            regionsLength: 0,
            loading: false
        }
    },
    computed: {
        ...mapClientFields([
            'client_data.address',
            'client_data.city',
            'client_data.zip',
            'client_data.country',
            'client_data.region'
        ])
    },
    watch: {
        regionsLength(value) {
            this.region = ''
            this.$emit('checkRequired', value)
        }
    },
    mounted() {
        const vm = this;
        eventBus.$on('saveSettings', () => {
            vm.$v.$touch()
            if (vm.$v.$invalid) {
                vm.validate()
                return
            }
            eventBus.$emit('validate', true)
        })
    },
    beforeDestroy() {
        eventBus.$off('saveSettings')
    },
    methods: {
        validationMessage: validationMessage(formMessages)
    }
}
</script>

