<template lang="html">
    <main-layout :settings-breadcrumbs="settingsBreadcrumbs">
        <template #content>
            <profile-form-layout
                :show-modal="showModal"
                @validate="validate"
                @close-modal="showModal=false"
                @confirm="confirm"
            >
                <template #headerIcon>
                    <home />
                </template>
                <template #form>
                    <div class="profile-info__form">
                        <div class="profile-info__form-group">
                            <div class="profile-info__form-row">
                                <div class="profile-info__col100">
                                    <Address
                                        @checkRequired="required = $event"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </template>
            </profile-form-layout>
        </template>
    </main-layout>
</template>

<script>
import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/profile/ProfileAddress/Rules'
import { formMessages } from '@/validation/profile/ProfileAddress/Messages'

import { eventBus } from '@/helpers/event-bus/'
import MainLayout from '@/layouts/MainLayout';
import ProfileFormLayout from '@/layouts/profile/ProfileFormLayout';
import Home from 'mdi-vue/Home'
import Address from '@/components/account-settings/ProfileAddress/Address';
import {
    mapClientFields
} from '@/store/modules/client';
import { createNamespacedHelpers } from 'vuex';
import {
    ACTION_CLIENT_ASK_PASSWORD,
    ACTION_CLIENT_UPDATE_ADDRESS
} from '@/store/modules/client/action-types';

const {
    mapActions: mapClientActions
} = createNamespacedHelpers('client');

export default {
    components: {
        MainLayout,
        Address,
        ProfileFormLayout,
        Home
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    data() {
        return {
            showModal: false,
            required: null,
            settingsBreadcrumbs: {
                title: 'Address',
                steps: [
                    { url: '/', title: 'Home' },
                    { url: '/cp/account', title: 'Account' },
                    { url: '/cp/profile/address', title: 'Change Address' }
                ],
                additionalClass: 'bc-wrapper bc-wrapper_user-account bc-wrapper--setting bc-right-bottom bc-horizontal'
            }
        }
    },
    validations: {
        ...validationRules
    },
    computed: {
        ...mapClientFields([
            'client_data.address',
            'client_data.city',
            'client_data.zip',
            'client_data.country',
            'client_data.region'
        ])
    },
    mounted() {
        eventBus.$on('validate', () => {
            this.validate()
        })
    },
    methods: {
        validationMessage: validationMessage(formMessages),
        ...mapClientActions({
            askForPassword: ACTION_CLIENT_ASK_PASSWORD,
            updateAddress: ACTION_CLIENT_UPDATE_ADDRESS
        }),
        confirm() {
            try {
                this.updateAddress({
                    address: this.address,
                    city: this.city,
                    zip: this.zip,
                    country: this.country,
                    region: this.region
                })
                eventBus.$emit('showSnackBar', 'Your address has been changed', 'success');
                this.$router.push({ name: 'profile' })
            } catch (error) {
                eventBus.$emit('showSnackBar', error.response.data.message, 'error');
            }
        },
        submit() {
            this.showModal = true
        }
    }
}
</script>
